<template>
   <v-container fill-height justify-center fluid v-if="!검색완료">
     <h1>썸썸이력</h1>
  </v-container>
  <v-container fill-height justify-center fluid v-else>
     <h1>썸썸이력</h1>
     <v-row>
        <v-col cols="12">
            <v-list height="400">
              <v-list-item v-for="(item, index) in 찾은목록" :key="`finded-${index}`">
                    <v-list-item-content>
                        <v-list-item-title>누군가 내 이름을 썸썸체크했어요!</v-list-item-title>
                        <v-list-item-subtitle>{{item.검색일.toDate()}}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-col>
     </v-row>
    </v-container>
</template>

<script>
import * as firestore from "firebase/firestore"
export default {
    name: 'Dashboard',
    data(){
      return{
        이름:"",
        찾은목록:[],
        modal:{
            userDetails:false
        },
        selectedDetails:null
      }
    },
    computed:{
        검색완료(){
            return this.찾은목록.length
        }
    },
    watch:{
    },
    mounted(){
      setTimeout(this.getHistory,1000)

    },
    methods:{
      async getHistory(){
        try {
            const uid = this.$currentUser.uid
            const docRef = firestore.doc(this.$db, "사용자", uid);
            const docSnap = await firestore.getDoc(docRef);
            let myName = null
            if (docSnap.exists()) {
                myName = docSnap.data().이름
            } else {
                console.log("No such document!");
            }
            const getTarget = firestore.query(firestore.collection(this.$db, "검색이력"), firestore.where("대상자이름", "==", myName));
            const querySnapshot = await firestore.getDocs(getTarget);
            const arr = []
            await querySnapshot.forEach((doc) => {
                arr.push({...doc.data(),id:doc.id});
            });
            this.찾은목록 = arr
            // const 찾음 = !!(querySnapshot.isEmpty)
        } catch (e) {
            console.error(e)
        }
      },
    }

}
</script>

